.container {
  margin-top: 100px;
}
.m-auto .btn-filter-film {
  border-radius: 40px;
}
.title {
  color: #f1b722;
}
/* card-movie */
.cards-film {
  text-align: center;
}
.cards-film .col-md-3 {
  margin-bottom: 40px;
}
.movie-item {
  vertical-align: auto;
}
.movie-item__img {
  width: 168px;
  height: 256px;
  border-radius: 5px;
}
.movie-item__img:hover {
  opacity: 0.8;
}
.movie-item__title {
  margin-top: 5px;
  color: white;
}
/* end card-movie */

@media screen and (max-width: 1200px) {
  .row-filter {
    text-align: center;
  }
  .left-side {
    max-width: none !important;
    flex: auto !important;
  }
  .hint-1 {
    display: none;
  }
}
@media screen and (max-width: 992px) {
  .cards-film .col-md-3 {
    max-width: 33.333%;
  }
}
@media screen and (max-width: 768px) {
  .cards-film .col-md-3 {
    max-width: 100%;
  }
}
@media screen and (max-width: 540px) {
  .movie-item__img {
    width: 250px;
    height: 360px;
  }
  iframe {
    margin: -20px;
  }
}
