.header-content-block {
  margin-top: 200px;
  color: #fff;
}
.title__movie {
  font-size: 68px;
  line-height: 60px;
  margin-bottom: 20px;
}
.movie__image {
  width: 340px;
  height: 500px;
  background-size: cover;
  border-radius: 15px;
  background-repeat: no-repeat;
}
.time {
  display: flex;
}
.director {
  display: flex;
}
.preview__label {
  margin-right: 20px;
}
.preview__film {
  margin-bottom: 30px;
}

@media only screen and (max-width: 1024px) and (min-width: 768px) and (orientation: portrait) {
  /* iPad portrait style here */
  .movie__image {
    margin-left: 165px;
  }
}
